<template>
  <div class="w-compare">
    <div class="compare-container">
      <div class="compare-target">
        <div>
          <a-upload
            :fileList="fileTarget"
            name="avatar"
            list-type="picture-card"
            class="avatar-uploader"
            @preview="handlePreview"
            @change="handleTargetChange"
            :before-upload="beforeUpload"
            :action="uploadUrl"
          >
            <div v-if="fileTarget.length == 0">
              <LoadingOutlined v-if="targetLoading" />
              <plus-outlined v-else />
              <div class="ant-upload-text">上传</div>
            </div>
          </a-upload>
        </div>
      </div>
      <div class="compare-origin">
        <div>
          <div>
            <a-upload
              :fileList="fileOrigin"
              name="avatar"
              list-type="picture-card"
              class="avatar-uploader"
              @preview="handlePreview"
              @change="handleOriginChange"
              :before-upload="beforeUpload"
              :action="uploadUrl"
            >
              <div
                v-if="single ? fileOrigin.length == 0 : fileOrigin.length < 20"
              >
                <LoadingOutlined v-if="originLoading" />
                <plus-outlined v-else />
                <div class="ant-upload-text">上传</div>
              </div>
            </a-upload>
          </div>
        </div>
      </div>
      <a-modal :visible="previewVisible" :footer="null" @cancel="handleCancel">
        <img style="width: 100%" :src="previewImage" />
      </a-modal>
    </div>
    <div class="code">
      <a-input v-model:value="code" style="width: 100px;margin-right: 20px;" />
      <img src="../assets/logo.png" width="100" height="30" />
    </div>
    <div>
      <a-button @click="submitData" :disabled="loading">
        {{ submitText }}
      </a-button>
    </div>
  </div>
</template>

<script>
import { PlusOutlined, LoadingOutlined } from "@ant-design/icons-vue";
function getBase64(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = (error) => reject(error);
  });
}

export default {
  name: "w-compare",
  components: {
    PlusOutlined,
    LoadingOutlined,
  },
  props: {
    single: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      targetLoading: false,
      originLoading: false,
      loading: false,
      fileTarget: [],
      fileOrigin: [],
      code: "",
      previewImage: "",
      previewVisible: false,
      uploadUrl: "https://www.mocky.io/v2/5cc8019d300000980a055e76",
    };
  },
  computed: {
    submitText() {
      if (this.loading) {
        return "比对中...";
      } else {
        return "提交比对";
      }
    },
  },
  methods: {
    submitData() {
      this.loading = !this.loading;
    },
    handleTargetChange({ fileList }) {
      this.fileTarget = fileList;
      this.targetLoading = false;
    },
    handleOriginChange({ fileList }) {
      this.fileOrigin = fileList;
      this.originLoading = false;
    },
    handleCancel() {
      this.previewVisible = false;
    },
    async handlePreview(file) {
      if (!file.url && !file.preview) {
        file.preview = await getBase64(file.originFileObj);
      }
      this.previewImage = file.url || file.preview;
      this.previewVisible = true;
    },
    beforeUpload(file) {
      const isJpgOrPng =
        file.type === "image/jpeg" ||
        file.type === "image/png" ||
        file.type === "image/jpg";
      if (!isJpgOrPng) {
        this.$message.error("您必须上传正确格式的图片!");
      }
      const isLt2M = file.size / 1024 / 1024 < 10;
      if (!isLt2M) {
        this.$message.error("图片不能大于10MB!");
      }
      return isJpgOrPng && isLt2M;
    },
  },
};
</script>

