  width: 100%;
  display: flex;
  .compare-target {
    justify-content: center;
    width: 50%;
    display: flex;
    align-items: center;
  }
  .compare-origin {
    justify-content: center;
    width: 50%;
    display: flex;
    align-items: center;
  }
  .ant-upload-picture-card-wrapper.avatar-uploader {
    display: flex;
  }
}
